:root {
  --main-bg-color: #333;
  --main-color: #ffd000;
  --font: "Roboto Mono", monospace;
}

div {
  display: inline-block;
}

body {
  background-color: var(--main-bg-color);
  color: var(--main-color);
  font-family: var(--font);
}

#container {
  grid-template: "main main"
                 "controls help"
                 "footer footer"
                 / 50% 50%;
  gap: 3rem 2rem;
  max-width: 60rem;
  margin: 2rem auto;
  display: grid;
}

#waveform {
  width: 100%;
  height: 100%;
  position: relative;
}

.editor {
  border: 1px solid;
  border-color: var(--main-color);
  grid-area: main;
}

.controls {
  grid-area: controls;
}

.help {
  grid-area: help;
}

.footer {
  text-align: center;
  grid-area: footer;
  font-size: .7rem;
}

.linethrough {
  text-decoration: line-through;
}

h3 {
  text-transform: uppercase;
  letter-spacing: .4rem;
  background-color: var(--main-color);
  color: var(--main-bg-color);
  padding: .4rem;
  font-size: 1rem;
  font-weight: 700;
}

a {
  color: var(--main-color);
}

.shortcut {
  vertical-align: middle;
  margin: 1rem 0;
}

.shortcut-icon {
  background-color: var(--main-color);
  color: var(--main-bg-color);
  box-sizing: border-box;
  text-align: center;
  vertical-align: middle;
  border: 2px inset #ffffff80;
  border-color: #ffffff80 #ffffff80 #00000080 #00000080;
  width: 2rem;
  height: 2rem;
  display: inline-block;
}

button {
  background-color: var(--main-color);
  color: var(--main-bg-color);
  box-sizing: border-box;
  text-align: center;
  vertical-align: middle;
  text-transform: uppercase;
  letter-spacing: .1rem;
  border: 2px inset #ffffff80;
  border-color: #ffffff80 #ffffff80 #00000080 #00000080;
  margin-bottom: 10px;
  padding: 1rem;
  font-family: Roboto Mono;
  font-size: .9rem;
  display: inline-block;
}

.grey {
  background-color: #999;
}

.shortcut-icon div {
  vertical-align: middle;
}

#overlay {
  z-index: 2;
  background-color: #000c;
  width: 100%;
  height: 100%;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
}

#overlaytext {
  font-size: 10rem;
  font-weight: bolder;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.slice-controls {
  margin-top: 1rem;
}

.slice-controls input {
  background-color: var(--main-color);
  color: var(--main-bg-color);
  border: none;
  width: 4rem;
  margin: 0 .5rem 10px;
  padding: .5rem;
}

.algorithm-controls {
  background: #ffffff1a;
  border-radius: 4px;
  margin-bottom: 1rem;
  padding: 1rem;
}

.slider-container {
  align-items: center;
  gap: 1rem;
  margin-bottom: 1rem;
  display: flex;
}

.slider-container label {
  min-width: 120px;
}

.slider-container input[type="range"] {
  flex: 1;
}

.slider-value {
  text-align: right;
  min-width: 50px;
}

input[type="range"] {
  -webkit-appearance: none;
  background: #ddd;
  border-radius: 5px;
  outline: none;
  height: 5px;
}

input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  cursor: pointer;
  background: #ffd000;
  border-radius: 50%;
  width: 15px;
  height: 15px;
}

.wavesurfer-loading {
  pointer-events: none;
  position: relative;
}

.wavesurfer-loading:after {
  content: "Recalculating...";
  color: #666;
  z-index: 9999;
  background: #fffc;
  justify-content: center;
  align-items: center;
  font-size: 1.2em;
  display: flex;
  position: absolute;
  inset: 0;
}

section button {
  margin-bottom: 10px;
}

button:disabled, .slice-controls input:disabled {
  opacity: .5;
  cursor: not-allowed;
  background-color: #999;
}

.message-overlay {
  background-color: var(--main-color);
  color: var(--main-bg-color);
  z-index: 1000;
  opacity: 0;
  border-radius: 4px;
  padding: 1rem;
  transition: opacity .3s ease-in-out;
  display: none;
  position: fixed;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
}

.message-overlay.show {
  opacity: 1;
  display: block;
}
